import {createGlobalStyle} from 'styled-components'
import {theme} from './theme'

export const GlobalStyle = createGlobalStyle`
  *,
  *::before,
  *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: ${theme.colors.primaryBg};

    @media (prefers-reduced-motion: reduce) {
      animation: none;
    }
  }
 //   
  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px ${theme.colors.buttonBg} inset !important;
    color: ${theme.colors.accent};
  }
  
  //   
  *::selection {
    color: ${theme.colors.contactColor};
    background-color: ${theme.colors.buttonBg};
  }

  //   
  *:focus-visible {
    outline: 2px solid ${theme.colors.borderColor};
  }

  //    FireFox
  * {
    scrollbar-width: thin;
    scrollbar-color: ${theme.colors.primaryTextColor} ${theme.colors.primaryBg};
  }

  //    Chrome
  *::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }

  *::-webkit-scrollbar-track,
  *::-webkit-scrollbar-track:horizontal {
    background-color: ${theme.colors.primaryBg};
  }

  *::-webkit-scrollbar-thumb,
  *::-webkit-scrollbar-thumb:horizontal {
    background-color: ${theme.colors.primaryTextColor};
    border: 3px solid ${theme.colors.primaryBg};
    border-radius: 5px;

    &:hover {
      background-color: ${theme.colors.accent};
    }
  }


  body {
    margin: 0;
    font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: ${theme.colors.primaryTextColor};
    line-height: 1.4;

    min-width: 360px;
  }

  a {
    text-decoration: none;
    cursor: pointer;
  }

  ul {
    list-style: none;
  }

  button {
    background-color: unset;
    border: none;
    cursor: pointer;
    color: ${theme.colors.accent};
  }

  section {
    padding: 100px 0;

    @media ${theme.media.mobile} {
      padding: 50px 0;
    }
  }

  label {
    cursor: pointer;
  }

`